<template>
    <div>
        
        <transition name="fade">
            <div>
                <div class="waihuiInfo" v-if="show">
                    <ul>
                        <li><span>外汇</span><span>现钞买入价</span><span>现钞卖出价</span></li>
                        <li v-for="item in waihui" >
                            <span>{{item.name}}</span>
                            <span>{{item.chao_in}}</span>
                            <span>{{item.chao_out}}</span>
                        </li>
                    </ul>
                </div>
            </div> 
        </transition>

    </div>
</template>
<script>

import {mapActions} from "vuex";

require('assets/css/base.scss')

export default {
    data() {
        return {
            house: [],
            show: true,
            noInfo: false,
            waihui: []
        }
    },
    methods: {
        houseSearch(e) {
            debugger
            if(isNaN(parseInt(e.currentTarget.value))) {
                alert("请输入数字");
                return false;
            }
            this.$data.show  = false;

            //先清空数据
            document.querySelector('.indicator').classList.remove("hide_it");
            this.$data.house = [];

            this.get_house_info([(res)=> {
                this.$data.noInfo = false;
                if(res.length >= 1) {
                    
                    res.forEach((item) => {
                        this.$data.house.push(item);
                    })
                    this.$data.show = true;
                } else {
                    this.$data.noInfo = true;
                }
                document.querySelector('.indicator').classList.add("hide_it");
            }, "?price=" + e.currentTarget.value]);
        },

        ...mapActions([
            "get_waihui_info",
        ])
    },
    components: {
    },
    beforeCreated() {
    },
    created() {


            //获取外汇接口信息
            this.get_waihui_info((res)=> {
                if(res.list.length >= 1) {
                    res.list.forEach((item) => {
                        this.$data.waihui.push(item);
                    })
                }
                document.querySelector('.indicator').classList.add("hide_it");
            })
    },
    beforeUpdated() {
        
    },
    updated() {
        debugger
    },
    beforeMounted() {
        debugger
    },
    mounted() {
        debugger
         document.querySelector('.indicator').classList.remove("hide_it");
    }
}
</script>
<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}


</style>



