// import { SET_MENU } from '../mutation-types'
import {
    fetchWaihui,
    fetchApi
} from './api.js'

let state = {
    get_waihui_info: false,
    get_house_info: false
}

const store = {
    state: state,
    actions: {
        get_waihui_info : ({commit}, fn) => {
            var info = fetchApi("waihui", fn);
            commit('GET_WAIHUI_INFO', true);
        },
        get_house_info : ({commit}, argu) => {
            
            var keyWord= "house";
            if(argu[1]) {
                keyWord = keyWord + argu[1]
            }
            debugger
            var info = fetchApi(keyWord, argu[0] ? argu[0]: argu);
            commit('GET_HOUSE_INFO', true);
        },
        get_dianzan_info: ({commit}, argu) => {
            
            var keyWord= "dianzan";
            if(argu[1]) {
                keyWord = keyWord + argu[1]
            }
            debugger
            var info = fetchApi(keyWord, argu[0] ? argu[0]: argu);
            //commit('GET_HOUSE_INFO', true);
        },

    },
    mutations: {
        ['GET_WAIHUI_INFO'](state, flag) {
            state.get_waihui_info = flag;
        },
        ['GET_HOUSE_INFO'](state, flag) {
            state.get_house_info = flag;
        },
    },
    getters: {
       /* iframe_url: ( state ) => {
            return state.iframe_url;
        },
        iframe_title: (state) => {
            return state.iframe_title;
        } */
    }
}



export default store


